import React from "react";

import Select from "react-select";
import makeAnimated from "react-select/animated";

// Bootstrap
import Row from "react-bootstrap/Row";
import Container from "react-bootstrap/Container";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";

// Routing
import { HashRouter as Router, Link } from "react-router-dom";

// Styles
import "./search.css";

function Search() {
  const animatedComponents = makeAnimated();

  var places = [
    {
      id: 0,
      label: "Sydney",
      value: 0,
      // disabled: false,
    },
    {
      id: 1,
      label: "India",
      value: 1,

      // disabled: false,
    },
    {
      id: 2,
      label: "America",
      value: 2,

      // disabled: false,
    },
  ];
  var services = [
    {
      id: 0,
      label: "Doctor",
      value: 0,
      // disabled: false,
    },
    {
      id: 1,
      label: "Cosmetics",
      value: 1,

      // disabled: false,
    },
    {
      id: 2,
      label: "Saloon",
      value: 2,

      // disabled: false,
    },
  ];
  return (
    <Container className="srch-container">
      <Row>
        <Col sm={6} lg={4}>
          <Select
            options={places}
            components={animatedComponents}
            placeholder="Location . . "
            className="srch-box"
          />
        </Col>
        <Col sm={6} lg={4}>
          <Select
            options={services}
            components={animatedComponents}
            placeholder="Service . . "
            isMulti
            className="srch-box"
          />
        </Col>
        <Col sm={12} lg={4}>
          <Router>
            <Link to="/result">
              <Button className="srch-btn mx-auto d-block"> Search </Button>
            </Link>
          </Router>
        </Col>
      </Row>
    </Container>
  );
}

export default Search;
