import React from "react";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Badge from "react-bootstrap/Badge";

// Icons
import { IoMdCalendar } from "react-icons/io";

// Styles
import "./resultcard.css";
import Button from "react-bootstrap/Button";

function ResultCard() {
  return (
    <div className="result-card">
      <Row>
        <Col sm={12} lg={3}>
          <img
            className="result-card-img"
            alt="img"
            src="https:/picsum.photos/200/200"
          />
        </Col>

        <Col sm={12} lg={6}>
          <h4>Name</h4>
          <p>
            Category <br />
            Description <br />
          </p>
          <p>
            <b>Location</b> <br />
            <a href="#">Organization Name/Clinic Name/Parlour Name/Brand</a>
            <br />
            Charges <br />
            <div className="feedback">
              <Badge variant="success rating">üëç 96%</Badge>

              <span className="reviews">50 patient stories</span>
            </div>
          </p>
        </Col>

        <Col sm={12} lg={3}>
          <p className="mx-auto d-block text-center">
            <IoMdCalendar />
            &nbsp; Available on "Timing"
          </p>
          <Button className="result-btn" variant="primary">
            Book Appointment
          </Button>{" "}
          <br />
          <Button className="result-btn" variant="outline-primary">
            Call
          </Button>
        </Col>
      </Row>
    </div>
  );
}

export default ResultCard;
