import React from "react";

import "./card.css";
import Button from "react-bootstrap/Button";

function Card() {
  return (
    <div className="card">
      <img alt="card-img" src="https://picsum.photos/200/200" />
      <div className="card-info">
        <h4>Title</h4>
        <p>Description</p>
        <Button>Read More</Button>
      </div>
    </div>
  );
}

export default Card;
